import React from "react";
import "./Scss/Blog.scss";
import Earth from "../../images/blogback.png";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";


function Blog({ english, russian, uzbek, dataNews }) {

  const { t } = useTranslation();

  return (
    <div className="Blog">
      <div className="wrapper">
        <div className="nine-page">
          <h1 className="name">{t("blog1")}</h1>
          <div className="cards">
            {dataNews.slice(0, 1).map((data) => (
              <div className="col-6 card1" key={data.id}>
                <Link to={`/onenews/${data.id}`} className="body">
                  <img src={data.image1} alt="image" className="img1" />
                  <div className="texts">
                    <h3 className="names">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h3>
                    <p className="desc">{english && data.description_en}{russian && data.description_ru}{uzbek && data.description_uz}</p>
                    <p className="data">{data.date}</p>
                  </div>
                </Link>
              </div>
            ))}

            <div className="col-6 second-card">
              {dataNews.slice(1).map((data) => (
                <div className="col-6 card2" key={data.id}>
                  <Link to={`/onenews/${data.id}`} className="body">
                    <img src={data.image1} alt="image" className="img2" />
                    <h3 className="names">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h3>
                    <p className="data">{data.date}</p>
                  </Link>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <img src={Earth} alt="earth" className="earth" />
      <div className="contract"></div>
    </div>
  );
}

export default Blog;
