import React from "react";
import "./Scss/Citizenship.scss";
import { Link } from "react-router-dom";
import Stars from "../../images/stars.png";
import Earth from "../../images/Earth.png";
import { useTranslation } from "react-i18next";

function Citizenship() {

  const {t} = useTranslation();

  return (
    <div className="Citizenship">
      <div className="wrapper">
        <img src={Stars} alt="back-stars" className="stars-img" />
        <div className="title">{t("fuq1")}</div>
        <p className="text">{t("fuq2")}</p>
        <div className="btns">
          <Link to="/citizenship" className="explore">{t("fuq3")}</Link>
          <Link to="/" className="explore">{t("explore")}</Link>
        </div>
      </div>
      <img src={Earth} alt="earth" className="earth" />
    </div>
  );
}

export default Citizenship;
