import React, { useEffect, useState } from 'react';
import './Scss/AnimationCards.scss';
import Img1 from '../../images/card-image.png';
import Img2 from '../../images/card-image2.png';

function AnimationCards() {

  const dataCards = [
    { id: 1, image: Img1 },
    { id: 2, image: Img2 },
    { id: 3, image: Img1 },
    { id: 4, image: Img2 }
  ];

  const [scroll, setScroll] = useState(0)
  const [scrollTop, setScrollTop] = useState(0)

  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      window.addEventListener("scroll", () => {
        if (window.scrollY - entry.target.offsetTop > -window.innerHeight / 4) {
          setScroll(window.scrollY - entry.target.offsetTop + window.innerHeight / 4)
        }

        if (window.scrollY - entry.target.offsetTop > -window.innerHeight / 4) {
          setScrollTop(window.scrollY - entry.target.offsetTop + window.innerHeight / 4)
        }
      });
    });
  });

  useEffect(() => {
    const animation = document.getElementById("trayekt");
    observer.observe(animation);
  }, []);

  return (
    <div className={`AnimationCards`}>
      <h1 className='text'><div>swiftvisa</div><div>swiftvisa</div><div>swiftvisa</div></h1>
      <h1 className="text-back" style={{ height: `${0 + scroll / 50}%` }}><div>swiftvisa</div><div>swiftvisa</div><div>swiftvisa</div></h1>
      {dataCards.map((data) => (
        <div key={data.id} className="anim-card" style={{ bottom: `calc(${-35 * (dataCards.indexOf(data) + 1)}vw + ${scrollTop / 10}%)`, transform: `rotate(${(dataCards.indexOf(data) + 1) % 2 ? scrollTop / 80 : -scrollTop / 80}deg)` }}>
          <img src={data.image} alt="img" />
        </div>
      ))}
      <div className="cards">
        {dataCards.map((data) => (
          <div key={data.id} className={`col-4 img${dataCards.indexOf(data) + 1}`}>
            <img src={data.image} alt="img" />
          </div>
        ))}
      </div>
    </div>
  )
}

export default AnimationCards