import "./Scss/Header.scss";
import Slider from "react-slick";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import BusIcon from "../../images/bus.png";
import ShitIcon from "../../images/shit.png";
import Cursor from "../../images/cursor.png";
import CheckIcon from "../../images/check.png";
import Passport from "../../images/pasport.png";
import { Room, TouchAppOutlined } from "@mui/icons-material";
import { useTranslation } from "react-i18next";

function Header({ english, russian, uzbek, dataHeader }) {

  const [slideIndex, setSlideIndex] = useState(0);

  const settings = {
    dots: true,
    speed: 3000,
    infinite: true,
    // autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    // autoplaySpeed: 5000,
    pauseOnHover: false,
    beforeChange: (current, next) => setSlideIndex(next),    
  };

  const {t} = useTranslation();

  return (
    <div className="Header">
      <ul className="carousel-dots">
        {dataHeader.map((data) => (
          <li key={data.id} style={{height: `calc(${100/dataHeader.length}vh - 9vh)`}} className={`${dataHeader.indexOf(data) == slideIndex && "active"}`}></li>
        ))}
      </ul>
      <Slider {...settings} className="carousel">
        {dataHeader.map((data) => (
          <div key={data.id} className="wrapper">
            <img src={data.image} alt="back-img" className="back-img" />
            <div className="cards">
              <div className="col-7">
                <h1 className="title-name">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h1>
                <p className="desc">{english && data.description_en}{russian && data.description_ru}{uzbek && data.description_uz}</p>
                <div className="icons">
                  <div className="col-4">
                    <div className="icon">
                      <img src={BusIcon} alt="bus" />
                    </div>
                    <p>{t("head1")}</p>
                  </div>
                  <div className="col-4">
                    <div className="icon">
                      <img src={ShitIcon} alt="bus" />
                    </div>
                    <p>{t("head2")}</p>
                  </div>
                  <div className="col-4">
                    <div className="icon">
                      <img src={CheckIcon} alt="bus" />
                    </div>
                    <p>{t("head3")}</p>
                  </div>
                </div>
                <div className="location">
                  <Room className="location-icon" />
                  <p>{data.location}</p>
                </div>
              </div>
              <Link to="/citizenship" className="col-5 passport">
                <div className="circle"></div>
                <img src={Passport} alt="visa" />
                {/* <TouchAppOutlined className="cursor" /> */}
                <img src={Cursor} alt="cursor" className="cursor" />
                <div className="cursor-text">{t("cursor")}</div>
              </Link>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default Header;
