import React from 'react';
import './Scss/ContactModal.scss';

const ContactModal = ({ close, changeContactModal, invalidName, nameValue, changeName, invalidNumber, numberValue, changeNumber }) => {
    return (
        <>
            <div className='ContactModal'>
                <div className="forms">
                    <div className="col-6">
                        <input type="text" className={`forms-control ${invalidName && "red-line"}`} value={nameValue} onChange={(e) => changeName(e)} placeholder='Ismingiz nima?' />
                    </div>
                    <div className="col-6">
                        <input type="number" className={`forms-control ${invalidNumber && "red-line"}`} value={numberValue} onChange={(e) => changeNumber(e)} placeholder='Telefon raqamingiz' />
                    </div>
                </div>
                <button onClick={() => changeContactModal()}>Yuborish</button>
            </div>
            <div className="contrast1" onClick={() => close()}></div>
        </>
    );
};

export default ContactModal;