import React, { useEffect, useRef, useState } from 'react'
import './App.scss';
import axios from 'axios';
import Blog from './components/Blog/Blog';
import Tokyo from './components/Tokyo/Tokyo';
import Modal from './components/Modal/Modal';
import Dityo from './components/Dityo/Dityo';
import Flags from './components/Flags/Flags';
import About from './components/About/About';
import { useTranslation } from 'react-i18next';
import OneNews from './components/News/OneNews';
import Obtain from './components/Obtain/Obtain';
import Travel from './components/Travel/Travel';
import Footer from './components/Footer/Footer';
import Header from './components/Header/Header';
import Navbar from './components/Navbar/Navbar';
import { Route, Switch } from 'react-router-dom';
import { BrowserRouter } from 'react-router-dom';
import Contacts from './components/Contacts/Contacts';
import OneCitizen from './components/Obtain/OneCitizen';
import Trayekt from './components/AnimationCards/Trayekt';
import AboutCompany from './components/About/AboutCompany';
import Citizenship from './components/Citizenship/Citizenship';
import ScrollToTop from './components/ScrollToTop/ScrollToTop';
import ForeignCheck from './components/ForeignCheck/ForeignCheck';
import SecondPassport from './components/SecondPassport/SecondPassport';
import AnimationCards from './components/AnimationCards/AnimationCards';
import Loader from './components/Loader/Loader';


function App() {

  // API

  const baseUrl = `https://swift.abba.uz/api`;
  const urlCountrySlider = `${baseUrl}/countryslider/`;
  const urlDityoSlider = `${baseUrl}/dityoslider/`;
  const urlHeader = `${baseUrl}/headerslider/`;
  const urlCountry = `${baseUrl}/countries/`;
  const urlTravel = `${baseUrl}/travel/`;
  const urlNews = `${baseUrl}/news/`;

  // CountrySlider API

  const [dataCountrySlider, setDataCountrySlider] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlCountrySlider);
      setDataCountrySlider(request.data);
      return request;
    };
    fetchData()
  }, [urlCountrySlider]);

  // DityoSlider API

  const [dataDityoSlider, setDataDityoSlider] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlDityoSlider);
      setDataDityoSlider(request.data);
      return request;
    };
    fetchData()
  }, [urlDityoSlider]);

  // Header API

  const [dataHeader, setDataHeader] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlHeader);
      setDataHeader(request.data);
      return request;
    };
    fetchData()
  }, [urlHeader]);

  // News API

  const [dataNews, setDataNews] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlNews);
      setDataNews(request.data);
      return request;
    };
    fetchData()
  }, [urlNews]);

  // Travel API

  const [dataTravel, setDataTravel] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlTravel);
      setDataTravel(request.data);
      return request;
    };
    fetchData()
  }, [urlTravel]);

  // Country API

  const [dataCountry, setDataCountry] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlCountry);
      setDataCountry(request.data);
      return request;
    };
    fetchData()
  }, [urlCountry]);

  // languages

  const { i18n } = useTranslation();

  const [english, setEnglish] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "en" ? true : false : false);
  const [russian, setRussian] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "ru" ? true : false : false);
  const [uzbek, setUzbek] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng') == "uz" ? true : false : false);
  const [langTitle, setLangTitle] = useState(localStorage.getItem('i18nextLng') ? localStorage.getItem('i18nextLng').toUpperCase() : "")

  function changeUzbek(item) {
    setUzbek(item)
    setEnglish(!item)
    setRussian(!item)
    i18n.changeLanguage("uz")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  function changeRussian(item) {
    setUzbek(!item)
    setRussian(item)
    setEnglish(!item)
    i18n.changeLanguage("ru")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  function changeEnglish(item) {
    setUzbek(!item)
    setEnglish(item)
    setRussian(!item)
    i18n.changeLanguage("en")
    setLangTitle(localStorage.getItem('i18nextLng').toUpperCase())
  }

  // countries API

  const urlCountries = `https://restcountries.com/v3.1/all/`;

  const [dataCountries, setDataCountries] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlCountries);
      setDataCountries(request.data);
      return request;
    };
    fetchData()
  }, [urlCountries]);

  // search filter

  const [inputValue, setInputValue] = useState("")

  function filterData(e) {
    setInputValue(e.target.value)
  }

  function selectData(item) {
    setInputValue(item)
  }

  const search = (data, item) => {
    if (english) {
      return data?.filter(post => post?.name.common?.toLowerCase().includes(inputValue.toLowerCase()))
    } else if (russian) {
      return data?.filter(post => post?.translations.rus.common?.toLowerCase().includes(inputValue.toLowerCase()))
    } else if (uzbek) {
      return item?.filter(post => post?.name_uz?.toLowerCase().includes(inputValue.toLowerCase()))
    }
  }

  // modal

  const [showModal, setShowModal] = useState(false)

  function changeModal() {
    setShowModal(!showModal)
  }

  // contact modal

  const [showContactModal, setShowContactModal] = useState(false)

  function changeContactModal() {
    setShowContactModal(!showContactModal)
  }

  // scrolls

  const abt = useRef();
  const blogs = useRef();
  const trav = useRef();

  function changeScrollBlog() {
    setTimeout(() => {
      window.scrollTo(0, blogs.current.offsetTop)
    }, 100);
  }

  function changeScrollAbt() {
    setTimeout(() => {
      window.scrollTo(0, abt.current.offsetTop)
    }, 100);
  }

  function changeScrollTrav() {
    setTimeout(() => {
      window.scrollTo(0, trav.current.offsetTop)
    }, 100);
  }

  return (
    <div>
      <BrowserRouter>
        <ScrollToTop />
        {/* <Loader /> */}
        <Navbar changeScrollTrav={changeScrollTrav} changeScrollAbt={changeScrollAbt} changeScrollBlog={changeScrollBlog} changeUzbek={changeUzbek} changeEnglish={changeEnglish} changeRussian={changeRussian} langTitle={langTitle} />
        <Switch>
          <Route exact path='/'>
            <Header english={english} russian={russian} uzbek={uzbek} dataHeader={dataHeader} />
            <Citizenship />
            <ForeignCheck showContactModal={showContactModal} changeContactModal={changeContactModal} changeModal={changeModal} filterData={filterData} selectData={selectData} dataCountries={search(dataCountries, dataCountry)} inputValue={inputValue} english={english} russian={russian} uzbek={uzbek} />
            <Dityo dataDityoSlider={dataDityoSlider} english={english} russian={russian} uzbek={uzbek} />
            <Flags english={english} russian={russian} uzbek={uzbek} />
            <div ref={abt}>
              <About />
            </div>
            <div ref={trav}>
              <SecondPassport english={english} russian={russian} uzbek={uzbek} dataTravel={dataTravel} />
            </div>
            <Trayekt />
            <AnimationCards />
            <div ref={blogs}>
              <Blog english={english} russian={russian} uzbek={uzbek} dataNews={dataNews} />
            </div>
            <Tokyo english={english} russian={russian} uzbek={uzbek} dataCountrySlider={dataCountrySlider} />
          </Route>
          <Route path="/about">
            <AboutCompany />
          </Route>
          <Route path="/onenews/:id">
            <OneNews english={english} russian={russian} uzbek={uzbek} dataNews={dataNews} />
          </Route>
          <Route path="/travels/:id">
            <Travel english={english} russian={russian} uzbek={uzbek} dataTravel={dataTravel} showContactModal={showContactModal} changeContactModal={changeContactModal} changeModal={changeModal} />
          </Route>
          <Route path="/citizenship">
            <Obtain showContactModal={showContactModal} changeContactModal={changeContactModal} changeModal={changeModal} dataTravel={dataTravel} english={english} russian={russian} uzbek={uzbek} dataCountry={dataCountry} />
          </Route>
          <Route path="/citizen/:id">
            <OneCitizen showContactModal={showContactModal} changeContactModal={changeContactModal} changeModal={changeModal} english={english} russian={russian} uzbek={uzbek} dataCountry={dataCountry} />
          </Route>
        </Switch>
        {showModal &&
          <Modal uzbek={uzbek} russian={russian} english={english} changeModal={changeModal} />
        }
        <Contacts changeModal={changeModal} />
        <Footer changeScrollAbt={changeScrollAbt} changeScrollBlog={changeScrollBlog} changeScrollTrav={changeScrollTrav} />
      </BrowserRouter>
    </div>
  )
}

export default App