import React from "react";
import { useState } from "react";
import Slider from "react-slick";
import "./Scss/SecondPassport.scss";
import { Link } from "react-router-dom";
import Earth from "../../images/Earth.png";
import { East } from "@mui/icons-material";
import { useTranslation } from "react-i18next";

function SecondPassport({ english, russian, uzbek, dataTravel }) {

  const [slideIndex, setSlideIndex] = useState(0);

  const settings = {
    dots: true,
    speed: 1500,
    infinite: true,
    autoplay: true,
    slidesToShow: 3,
    centerMode: true,
    slidesToScroll: 1,
    autoplaySpeed: 5000,
    centerPadding: "0px",
    beforeChange: (current, next) => setSlideIndex(next),
    responsive: [
      {
        breakpoint: 600,
        settings: {
          dots: false,
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  const {t} = useTranslation();

  return (
    <div className="SecondPassport">
      <div className="wrapper">
        <h1 className="title">{t("zachem1")}</h1>        
        <Slider {...settings} className="carousel">
          {dataTravel.map((data, index) => (
            <div className={index === slideIndex ? "slide slide-active" : "slide"} key={data.id}>
              <div className="body">
                <img src={data.image1} alt="img" />
                <div className="text">
                  <h3 className="name">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h3>
                  <Link to={`/travels/${data.id}`} className="explore">{t("explore2")} <East className="icon" /></Link>
                </div>
              </div>
            </div>
          ))}
        </Slider>
        <div className="texts col-12">
          <p className="text-desc">{t("zachem2")}</p>
        </div>
        <img src={Earth} alt="earth" className="yer" />
        <div className="contrast"></div>
      </div>
    </div>
  );
}

export default SecondPassport;
