import React, { useState } from 'react';
import './Scss/Travel.scss';
import { Link, useParams } from 'react-router-dom';
import { ArrowForward, ArrowOutward } from '@mui/icons-material';
import { useTranslation } from 'react-i18next';
import ContactModal from '../ContactModal/ContactModal';

const Travel = ({ english, uzbek, russian, dataTravel, changeModal, changeContactModal, showContactModal }) => {

    const { id } = useParams();

    // forms

    const [nameValue, setNameValue] = useState("");
    const [numberValue, setNumberValue] = useState("");
    const [invalidName, setInvalidName] = useState(false);
    const [invalidNumber, setInvalidNumber] = useState(false);

    function changeName(e) {
        setNameValue(e.target.value)
        setInvalidName(false)
    };

    function changeNumber(e) {
        setNumberValue(e.target.value)
        setInvalidNumber(false)
    };

    let bot = {
        TOKEN: "5796375072:AAEFP5ieQGW4bGvtg9UBdu_7n4304uS97Sw",
        chatID: "-1001830157246",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}.`
    };

    function sendMessage() {
        if (nameValue === "") {
            setInvalidName(true)
        } else if (numberValue === "") {
            setInvalidNumber(true)
        } else {
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method: "GET"
            })
                .then(success => {
                    console.log('send message')
                }, error => {
                    console.log(error)
                })

            changeContactModal()
            changeModal()
            setNameValue("")
            setNumberValue("")
        }
    };

    const { t } = useTranslation();

    return (
        <>
            <div className='Travel'>
                {dataTravel.filter((c) => c.id == id).map((data) => (
                    <div key={data.id} className="wrapper">
                        <div className="links">
                            <Link to="/" onClick={() => window.scrollTo(0, 0)}>{t("link1")}</Link>
                            <a href="#"> - {t("link4")}</a>
                        </div>
                        <div className="titles">
                            <h1 className='col-10 title'>{t("travel1")}</h1>
                            <div className="flag col-2">
                                <img src={data.flag} alt="flag" />
                                <p className='name'>{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</p>
                            </div>
                            {/* <div className="col-3 price">
                            <h3>{data.price}</h3>
                            <p>UZS</p>
                        </div> */}
                        </div>
                        <div className="imgs">
                            <div className="col-6 texts">
                                <p>{t("trav1")}</p>
                                <p>{t("trav2")}</p>
                                <p>{t("trav3")}</p>
                                <p>{t("trav4")}</p>
                                <p>{t("trav5")}</p>
                                <p>{t("trav6")}</p>
                                <p>{t("trav7")}</p>
                                <p>{t("trav8")}</p>
                            </div>
                            <div className="col-6 img main">
                                <img src={data.image1} alt="img1" />
                            </div>
                            {/* <div className="col-6 main">
                                <div className="col-6 img">
                                    <img src={data.image2} alt="img2" />
                                </div>
                                <div className="col-6 img">
                                    <img src={data.image3} alt="img3" />
                                </div>
                                <div className="col-6 img">
                                    <img src={data.image4} alt="img4" />
                                </div>
                                <div className="col-6 img">
                                    <img src={data.image5} alt="img5" />
                                </div>
                            </div> */}
                        </div>
                        <h1 className="title">{t("fuq10")}</h1>
                        <p className="desc">{english && data.description_en}{russian && data.description_ru}{uzbek && data.description_uz}</p>
                        <div className="explore">
                            <div className="link" onClick={() => changeContactModal()}>{t("explore3")} <ArrowForward className='line' /></div>
                        </div>
                    </div>
                ))}
            </div>
            {showContactModal &&
                <ContactModal close={changeContactModal} nameValue={nameValue} invalidName={invalidName} changeName={changeName} numberValue={numberValue} invalidNumber={invalidNumber} changeNumber={changeNumber} changeContactModal={sendMessage} />
            }
        </>
    );
};

export default Travel;