import React from "react";
import "./Scss/Tokyo.scss";
import Slider from "react-slick";
import { LocationOn } from "@mui/icons-material";
import { useTranslation } from "react-i18next";

function Tokyo({ english, russian, uzbek, dataCountrySlider }) {

  const settings = {
    dots: true,
    speed: 2000,
    infinite: true,
    autoplay: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplaySpeed: 5000,
  };

  const { t } = useTranslation();

  return (
    <div className="Tokyo">
      <Slider {...settings} className="carousel">
        {dataCountrySlider.map((data) => (
          <div className="body" key={data.id}>
            <img src={data.image} alt="img" className="back-img" />
            <div className="texts">
              <div className="location"><LocationOn className="location-icon" /> {data.location}</div>
              <h1 className="name">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h1>
              <p className="desc">{english && data.description_en}{russian && data.description_ru}{uzbek && data.description_uz}</p>
              <div className="link">
                <a href={data.link} className="explore">{t("explore")}</a>
              </div>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default Tokyo;
