import React, { useState, useEffect } from "react";
import CountUp from "react-countup";
import "./Scss/About.scss";
import TravelBackg from "../../images/travelBacg.png";
import AboutImg from "../../images/about.png";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

function About() {
  const [state1, setState1] = useState(0);
  const [state2, setState2] = useState(0);
  const [state3, setState3] = useState(0);

  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      window.addEventListener("scroll", () => {
        if (window.scrollY - entry.target.offsetTop > -window.innerHeight) {
          setState1(500);
          setState2(500);
          setState3(500);
        }else if (window.scrollY - entry.target.offsetTop < 0) {
          setState1(0);
          setState2(0);
          setState3(0);
        }
      });
    });
  });

  useEffect(() => {
    const about = document.getElementById("about");
    observer.observe(about);
  }, []);

  const {t} = useTranslation();

  return (
    <div className="About" id="about">
      <div className="wrapper">
        <div className="sixth-page">
          <img
            src={TravelBackg}
            alt="background-img"
            className="background-img"
          />
          <div className="contract"></div>
        </div>
        <div className="col-12">
          <div className="body">
            <h3>{t("about1")}</h3>
            <img src={AboutImg} alt="img" className="about-img" />
            <p>{t("about2")}</p>
          </div>
          <div className="more">
            <Link to="/about" className="explore">{t("about3")}</Link>
          </div>
        </div>
        <div className="count-up">
          <div className="col-4">
            <div className="body">
              <h1 className="number">
                <CountUp start={0} end={state1} duration={10} />
                +
              </h1>
              <p className="desc">{t("number1")}</p>
            </div>
          </div>
          <div className="col-4">
            <div className="body">
              <h1 className="number">
              <CountUp start={0} end={state2} duration={10} />
              +
              </h1>
              <p className="desc">{t("number2")}</p>
            </div>
          </div>
          <div className="col-4">
            <div className="body">
              <h1 className="number">
              <CountUp start={0} end={state3} duration={10} />
              +
              </h1>
              <p className="desc">{t("number3")}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;
