import React from "react";
import "./Scss/Dityo.scss";
import Slider from "react-slick";
import { Link } from "react-router-dom";
import TravelBackg from "../../images/travelBacg.png";
import { HeadphonesRounded } from "@mui/icons-material";
import { useTranslation } from "react-i18next";

function Dityo({ dataDityoSlider, english, russian, uzbek }) {
  const settings = {
    dots: true,
    infinity: true,
    speed: 1000,
    slidesToShow: 4,
    slidesToScroll: 2,
    responsive: [
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true
        }
      },
    ]
  };

  const {t} = useTranslation();

  return (
    <div className="Dityo">
      <div className="wrapper">
        <div className="fourth-page">
          <img
            src={TravelBackg}
            alt="background-img"
            className="background-img"
          />
          <div className="contract"></div>
        </div>
        <div className="dityo-header">
          <div className="names">
            <h1 className="name">{t("dityo1")}</h1>
            <p className="desc">{t("dityo2")}</p>
          </div>          
        </div>
        <div className="cards">
          <Slider {...settings} className="carousel">
            {dataDityoSlider.map((data) => (
              <div className="slide" key={data.id}>
                <div className="body">
                  <img src={data.image} alt="img" />
                  <p className="s-name">{uzbek && data.name_uz}{russian && data.name_ru}{english && data.name_en}</p>
                </div>
              </div>
            ))}
          </Slider>
        </div>
        <div className="bottom-texts col-12">
          <div className="head">
            {/* <HeadphonesRounded className="headphone-icon" />
            <p className="desc">{t("dityo3")}</p> */}
          </div>
          <Link to="/" className="explore">{t("explore")}</Link>
        </div>
      </div>
    </div>
  );
}

export default Dityo;
