import React, { useEffect, useState } from "react";
import "./Scss/Flags.scss";
import { Link } from "react-router-dom";
import Earth from "../../images/Earth.png";
import { useTranslation } from "react-i18next";
import axios from "axios";

function Flags({ english, russian, uzbek }) {

  const [activeId, setactiveId] = useState(1);

  // Continents API

  const urlContinents = `https://swift.abba.uz/api/continents/`;

  const [dataContinents, setDataContinents] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlContinents);
      setDataContinents(request.data);
      return request;
    };
    fetchData()
  }, [urlContinents]);

  // Flags API

  const urlFlags = `https://swift.abba.uz/api/flags/`;

  const [dataFlags, setDataFlags] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(urlFlags);
      setDataFlags(request.data);
      return request;
    };
    fetchData()
  }, [urlFlags]);

  useEffect(() => {
    let titles = document.querySelectorAll(".title");
    for (let i = 0; i < titles.length; i++) {
      titles[i].addEventListener("click", () => {
        titles.forEach((title) => {
          title.classList.remove("active");
        });
        titles[i].classList.add("active");
      });
    }
  }, []);

  const [activeIndex, setActiveIndex] = useState(1);

  function findIndex(item) {
    setActiveIndex(item);
  }

  console.log(activeIndex)

  const { t } = useTranslation();

  return (
    <div className="Flags">
      <div className="wrapper">
        <div className="fiveth-page">
          <h1 className="title-main">{t("partner-country")}</h1>
          <div className="items">
            {dataContinents.map((item) => (
              <div key={item.id}
                className={`title ${item.id == activeIndex && "active"}`}
                onClick={() => findIndex(item.id)}
              >
                {uzbek && item.name_uz}{english && item.name_en}{russian && item.name_ru}
              </div>
            ))}
          </div>
          <div className="flags">
            {dataFlags.map(
              (data) =>
                activeIndex === data.continent && (
                  <div key={data.id} className="col-3">
                    <div className="flag">
                      <img src={data.image} alt="flag" className="image" />
                      <p className="name">{uzbek && data.name_uz}{english && data.name_en}{russian && data.name_ru}</p>
                    </div>
                  </div>
                )
            )}
          </div>
          <div className="link">
            <Link to='/' className="explore">{t("explore")}</Link>
          </div>
        </div>
      </div>
      <img src={Earth} alt="earth" className="yer" />
    </div>
  );
}

export default Flags;
