import React from 'react';
import './Scss/OneNews.scss';
import { Link, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const OneNews = ({ english, uzbek, russian, dataNews }) => {

    const {id} = useParams();

    const {t} = useTranslation();

    return (
        <div className='OneNews'>
            {dataNews.filter((c) => c.id == id).map((data) => (
                <div key={data.id} className="wrapper">
                    <div className="links">
                        <Link to="/" onClick={() => window.scrollTo(0,0)}>{t("link1")}</Link>
                        <a href="#"> - {t("link2")}</a>
                    </div>
                    <h1 className="title">{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</h1>
                    <div className="cards">
                        <div className="body">
                            <div className="top-texts">
                                <div className="date">{data.date}</div>
                                <div className="name">{data.author}</div>
                            </div>
                            <p>{english && data.description_en}{russian && data.description_ru}{uzbek && data.description_uz}</p>
                            <img src={data.image1} alt="img" />                        
                            <div className="bottom">
                                <h1 className='date'></h1>
                                <h1 className="view"><i className="fa fa-eye"></i> 342</h1>
                            </div>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    );
};

export default OneNews;