import React, { useState } from "react";
import "./Scss/Navbar.scss";
import Img1 from "../../images/logo.png";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

const Navbar = ({ white, black, changeUzbek, changeEnglish, changeRussian, langTitle, changeScrollTrav, changeScrollAbt, changeScrollBlog }) => {
  window.addEventListener("scroll", function () {
    let navScroll = document.getElementById("nav");
    if (white) {
      navScroll.classList.toggle("white", window.scrollY > 10);
    } else if (!white) {
      navScroll.classList.toggle("black", window.scrollY > 10);
    }
  });
  const [show, setShow] = useState(false);

  // languages

  const [language, setLanguage] = useState(false);

  function changeHandleUzbek() {
    changeUzbek(true)
    setLanguage(!language)
  };

  function changeHandleEnglish() {
    changeEnglish(true)
    setLanguage(!language)
  }
  function changeHandleRussian() {
    changeRussian(true)
    setLanguage(!language)
  }

  const { t } = useTranslation();

  return (
    <div id={`nav`} className="Navbar">
      <nav className="navbar navbar-expand">
        <Link to="/" onClick={() => window.scrollTo(0, 0)} className="logo navbar-brand">
          <img src={Img1} alt="logo" />
        </Link>
        <ul className={`navbar-nav ${show && "show"}`}>
          <li className="nav-item">
            <Link to="/" className="nav-link" onClick={changeScrollAbt}>
              {t("nav2")}
            </Link>
          </li>
          <li className="nav-item">
            <Link to="/" className="nav-link" onClick={changeScrollTrav}>
              {t("nav1")}
            </Link>
          </li>
          <li className="nav-item">
            <Link to="/" className="nav-link" onClick={changeScrollBlog}>
              {t("nav3")}
            </Link>
          </li>
          <li className="nav-item">
            <a href="#contacts" className="nav-link">
              {t("nav4")}
            </a>
          </li>
          <li className="nav-item tel-item">
            <a href="tel:+99895-195-70-07" className="nav-link">
              {/* {t("nav5")} */}
              +998951957007
            </a>
          </li>
          <li className="nav-item">
            <div className="language">
              <input type="checkbox" id="language" checked={language} onChange={() => setLanguage(!language)} />
              <label htmlFor='language'>{langTitle} <i className="fa fa-caret-down"></i></label>
              <div className={`contarst ${!language && "d-none"}`} onClick={() => setLanguage(!language)}></div>
              <ul className={`language-menu ${!language && "d-none"}`}>
                <li onClick={() => changeHandleUzbek()}><div className='lang-link'>UZ</div></li>
                <li onClick={() => changeHandleEnglish()}><div className='lang-link'>ENG</div></li>
                <li onClick={() => changeHandleRussian()}><div className='lang-link'>RU</div></li>
              </ul>
            </div>
          </li>
          <i className="fa fa-times" onClick={() => setShow(false)}></i>
        </ul>
        <a href="tel:+99895-195-70-07" className="number">
          <div className="navbar-brand login">
            {/* {t("nav5")} */}
            +998951957007
          </div>
        </a>
        <div className="navbar-brand">
          <i className={`fa fa-bars`} onClick={() => setShow(true)}></i>
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
