import React, { useState } from 'react';
import './Scss/Contacts.scss'
import Img2 from '../../images/footer.png';
import Img1 from '../../images/travelBacg.png';
import { EmailOutlined, PhoneInTalkOutlined, WhatsApp } from '@mui/icons-material';
import { useTranslation } from 'react-i18next';

const Contacts = ({ changeModal }) => {

  const [nameValue, setNameValue] = useState("");
  const [textValue, setTextValue] = useState("");
  const [emailValue, setEmailValue] = useState("");
  const [numberValue, setNumberValue] = useState("");
  const [invalidName, setInvalidName] = useState(false);
  const [invalidNumber, setInvalidNumber] = useState(false);
  const [invalidEmail, setInvalidEmail] = useState(false);

  function changeName(e) {
    setNameValue(e.target.value)
    setInvalidName(false)
  };

  function changeNumber(e) {
    setNumberValue(e.target.value)
    setInvalidNumber(false)
  };

  function changeEmail(e) {
    setEmailValue(e.target.value)
    if (e.target.value != "" && e.target.value.includes("@") && e.target.value.includes(".") && e.target.value != "") {
      setInvalidEmail(false)
    }
  };

  function changeText(e) {
    setTextValue(e.target.value)
  };

  let bot = {
    TOKEN: "5796375072:AAEFP5ieQGW4bGvtg9UBdu_7n4304uS97Sw",
    chatID: "-1001830157246",
    message: `Ismi: ${nameValue}; %0ATelefon raqami: ${numberValue}; %0AEmaili: ${emailValue}; %0AXabar: ${textValue}`
  }

  function sendMessage() {
    if (nameValue === "") {
      setInvalidName(true)
    } else if (numberValue === "") {
      setInvalidNumber(true)
    } else if (emailValue != "" && !emailValue.includes("@") || !emailValue.includes(".") && emailValue != "") {
      setInvalidEmail(true)
    } else {
      fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
        method: "GET"
      })
        .then(success => {
          console.log('send message')
        }, error => {
          console.log(error)
        })

      changeModal();
      setNameValue("");
      setTextValue("");
      setEmailValue("");
      setNumberValue("");
    }
  };

  const { t } = useTranslation();

  return (
    <div className='Contacts' id='contacts'>
      <div className="wrapper">
        <div className="col-6 texts">
          <div className="body">
            <h1 className="title">{t("form1")}</h1>
            <p className="desc">{t("form2")}</p>
            <ul className='badges'>
              <li>
                <a href='tel:+99897-445-44-48' className="call-badge"><PhoneInTalkOutlined className='icon' /> +99897-445-44-48</a>
              </li>
              <li>
                <a href='tel:+99897-445-44-48' className="call-badge"><WhatsApp className='icon' /> (97)-445-44-48</a>
              </li>
              <li>
                <a href='mailto:swiftvisauz@gmail.com' className="call-badge"><EmailOutlined className='icon' /> swiftvisauz@gmail.com</a>
              </li>
            </ul>
          </div>
          <img className='statue' src={Img2} alt="haykal" />
        </div>
        <div className="col-6 forms">
          <div className="body">
            <h4>{t("form3")}</h4>
            <input type="text" className={`forms-control ${invalidName && "red-line"}`} value={nameValue} onChange={(e) => changeName(e)} placeholder={`${t("form4")}`} />
            <div className="inputs">
              <div className="col-6">
                <input type="number" className={`forms-control ${invalidNumber && "red-line"}`} value={numberValue} onChange={(e) => changeNumber(e)} placeholder={`${t("form5")}`} />
              </div>
              <div className="col-6">
                <input type="text" className={`forms-control ${invalidEmail && "red-line"}`} value={emailValue} onChange={(e) => changeEmail(e)} placeholder={`${t("form6")}`} />
              </div>
            </div>
            <textarea className={`forms-control`} rows="5" value={textValue} onChange={(e) => changeText(e)} placeholder={`${t("form7")}`}></textarea>
            <button onClick={() => sendMessage()}>{t("form8")}</button>
          </div>
        </div>
      </div>
      <img src={Img1} alt="back" className='back-img' />
    </div>
  );
};

export default Contacts;