import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './Scss/Obtain.scss';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import ContactModal from '../ContactModal/ContactModal';
import { ArrowForward, TrendingFlat } from '@mui/icons-material';

const Obtain = ({ english, russian, uzbek, dataCountry, dataTravel, changeModal, changeContactModal, showContactModal }) => {

    const urlCountries = `https://restcountries.com/v3.1/all/`;

    const [dataCountries, setDataCountries] = useState([]);

    useEffect(() => {
        async function fetchData() {
            const request = await axios.get(urlCountries);
            setDataCountries(request.data);
            return request;
        };
        fetchData()
    }, [urlCountries]);

    const [activeCountry, setActiveContry] = useState("Europe");

    const regions = [
        { id: 1, name_uz: "Yevropa", name_en: "Europe", name_ru: "" },
        { id: 2, name_uz: "Osiyo", name_en: "Asia", name_ru: "" },
        { id: 3, name_uz: "Afrika", name_en: "Africa", name_ru: "" },
        { id: 4, name_uz: "Janubiy Amerika", name_en: "South America", name_ru: " " },
        { id: 5, name_uz: "Shimoliy Amerika", name_en: "North America", name_ru: " " },
        { id: 6, name_uz: "Okeaniya", name_en: "Oceania", name_ru: "" },
        { id: 7, name_uz: "Antarktida", name_en: "Antarctica", name_ru: "" },
    ];

    function changeCountry(item) {
        setActiveContry(item)
    };

    // search filter

    const [inputValue, setInputValue] = useState("");
    const [showSearch, setShowSearch] = useState(false);

    function filterData(e) {
        setInputValue(e.target.value)
    }

    const search = (data, item) => {
        if (english) {
            return data?.filter(post => post?.name.common?.toLowerCase().includes(inputValue.toLowerCase()))
        } else if (russian) {
            return data?.filter(post => post?.translations.rus.common?.toLowerCase().includes(inputValue.toLowerCase()))
        } else if (uzbek) {
            return item?.filter(post => post?.name_uz?.toLowerCase().includes(inputValue.toLowerCase()))
        }
    }

    // forms

    const [nameValue, setNameValue] = useState("");
    const [numberValue, setNumberValue] = useState("");
    const [invalidName, setInvalidName] = useState(false);
    const [invalidNumber, setInvalidNumber] = useState(false);

    function changeName(e) {
        setNameValue(e.target.value)
        setInvalidName(false)
    };

    function changeNumber(e) {
        setNumberValue(e.target.value)
        setInvalidNumber(false)
    };

    let bot = {
        TOKEN: "5796375072:AAEFP5ieQGW4bGvtg9UBdu_7n4304uS97Sw",
        chatID: "-1001830157246",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}.`
    };

    function sendMessage() {
        if (nameValue === "") {
            setInvalidName(true)
        } else if (numberValue === "") {
            setInvalidNumber(true)
        } else {
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method: "GET"
            })
                .then(success => {
                    console.log('send message')
                }, error => {
                    console.log(error)
                })

            changeContactModal()
            changeModal()
            setNameValue("")
            setNumberValue("")
        }
    };

    const { t } = useTranslation();

    return (
        <>
            <div className="Obtain">
                <div className="wrapper">
                    <div className="links">
                        <Link to="/" onClick={() => window.scrollTo(0,0)}>{t("link1")}</Link>
                        <a href="#"> - {t("link3")}</a>
                    </div>
                    <div className="titles">
                        <div className="col-4">
                            <h1 className="title">{t("fuq4")}</h1>
                        </div>
                        <div className="col-4">
                            <p className='text'>{t("fuq5")}</p>
                        </div>
                        <div className="col-4 search">
                            <input value={inputValue} type="text" placeholder={`${t("search")}`} onClick={() => setShowSearch(true)} onChange={(e) => filterData(e)} />
                            <i className="fa fa-serch"></i>
                            <div className={`contrast ${!showSearch && "d-none"}`} onClick={() => setShowSearch(false)}></div>
                            <div className={`search-bar ${!showSearch && "d-none"}`}>
                                {search(dataCountries, dataCountry).map((data) => (
                                    <Link to={`/citizen/${uzbek ? dataCountry.indexOf(data) : dataCountries.indexOf(data)}`} className="result">{english && data.name.common}{russian && data.translations.rus.common}{uzbek && data.name_uz}</Link>
                                ))}
                            </div>
                        </div>
                    </div>
                    <div className="countries">
                        <ul className="col-4 continents">
                            {regions.map((data) => (
                                <li key={data.id} className={`continent ${activeCountry == data.name_en && "active"}`} onClick={() => changeCountry(data.name_en)}>{english && data.name_en}{russian && data.name_ru}{uzbek && data.name_uz}</li>
                            ))}
                        </ul>
                        <div className="col-8 flags">
                            {dataCountries.map((data, index) => (
                                data.continents == activeCountry &&
                                <div key={index} className="flag">
                                    <Link to={`/citizen/${dataCountries.indexOf(data)}`} className="body">
                                        <img src={data.flags.png} alt="flag" />
                                        {uzbek ?
                                            dataCountry.filter((c) => c.name_en == data.name.common).map((item) => (
                                                <p className="name">{item.name_uz}</p>
                                            ))
                                            : <p className="name">{english && data.name.common}{russian && data.translations.rus.common}</p>
                                        }
                                    </Link>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="bottom-titles col-12">
                        <h1 className="title col-8">{t("fuq6")}</h1>
                        <div className='explore' onClick={() => changeContactModal()}>{t("explore3")} <ArrowForward className='line' /></div>
                    </div>
                    <p className="bottom-texts col-5">{t("fuq7")}</p>
                    <div className="cards">
                        {dataTravel.map((data) => (
                            <div key={data.id} className="col-4">
                                <div className="body">
                                    <img src={data.image1} className="count-img" alt="country" />
                                    <Link to={`/travels/${data.id}`} className="about">
                                        <img src={data.flag} alt="flag" />
                                        <div className="texts">
                                            <h2 className="name">{data.name_en} <TrendingFlat className='right-icon' /></h2>
                                            <div className="desc">{data.description_en.slice(0, 54)}...</div>
                                        </div>
                                    </Link>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
            {showContactModal &&
                <ContactModal close={changeContactModal} nameValue={nameValue} invalidName={invalidName} changeName={changeName} numberValue={numberValue} invalidNumber={invalidNumber} changeNumber={changeNumber} changeContactModal={sendMessage} />
            }
        </>
    );
};

export default Obtain;