import axios from 'axios';
import './Scss/OneCitizen.scss';
import { Link, useParams } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import { ArrowForward } from '@mui/icons-material';
import { useTranslation } from 'react-i18next';
import ContactModal from '../ContactModal/ContactModal';

const OneCitizen = ({ changeModal, changeContactModal, showContactModal, english, russian, uzbek, dataCountry }) => {

    const { id } = useParams();

    const urlCountries = `https://restcountries.com/v3.1/all/`;
    const [dataCountries, setDataCountries] = useState([]);

    useEffect(() => {
        async function fetchData() {
            const request = await axios.get(urlCountries);
            setDataCountries(request.data);
            return request;
        };
        fetchData()
    }, [urlCountries]);

    // forms

    const [nameValue, setNameValue] = useState("");
    const [numberValue, setNumberValue] = useState("");
    const [invalidName, setInvalidName] = useState(false);
    const [invalidNumber, setInvalidNumber] = useState(false);

    function changeName(e) {
        setNameValue(e.target.value)
        setInvalidName(false)
    };

    function changeNumber(e) {
        setNumberValue(e.target.value)
        setInvalidNumber(false)
    };

    let bot = {
        TOKEN: "5796375072:AAEFP5ieQGW4bGvtg9UBdu_7n4304uS97Sw",
        chatID: "-1001830157246",
        message: `%0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}.`
    };

    function sendMessage() {
        if (nameValue === "") {
            setInvalidName(true)
        } else if (numberValue === "") {
            setInvalidNumber(true)
        } else {
            fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
                method: "GET"
            })
                .then(success => {
                    console.log('send message')
                }, error => {
                    console.log(error)
                })

            changeContactModal()
            changeModal()
            setNameValue("")
            setNumberValue("")
        }
    };

    const { t } = useTranslation();

    return (
        <>
            <div className='OneCitizen'>
                <div className="wrapper">
                    <div className="links">
                        <Link to="/" onClick={() => window.scrollTo(0, 0)}>{t("link1")}</Link>
                        <a href="#"> - {t("link3")}</a>
                    </div>
                    <h1 className="title">{t("fuq8")}</h1>
                    <p className="texts">{t("fuq9")}</p>
                </div>
                {dataCountries.filter((c) => dataCountries.indexOf(c) == id).map((data) => (
                    <div className="info">
                        <div className="col-5">
                            {uzbek ?
                                dataCountry.filter((c) => c.name_en == data.name.common).map((item) => (
                                    <h1 className="name title">{item.name_uz}</h1>
                                ))
                                : <h1 className="name title">{english && data.name.common}{russian && data.translations.rus.common}</h1>
                            }
                            <img src={data.flags.png} alt="" />
                        </div>
                        {dataCountry.filter((c) => c.name_en == data.name.common).map((item) => (
                            <div className="col-7">
                                <div className="data">
                                    <h5>{t("doc1")}</h5>
                                    <p>{english && item.document_1_en}{russian && item.document_1_ru}{uzbek && item.document_1_uz}</p>
                                </div>
                                <div className="data">
                                    <h5>{t("doc3")}</h5>
                                    <p>{english && item.document_2_en}{russian && item.document_2_ru}{uzbek && item.document_2_uz}</p>
                                </div>
                                <div className="data">
                                    <h5>{t("doc5")}</h5>
                                    <p>{english && item.document_3_en}{russian && item.document_3_ru}{uzbek && item.document_3_uz}</p>
                                </div>
                                <div className="data">
                                    <h5>{t("doc7")}</h5>
                                    <p>{english && item.document_4_en}{russian && item.document_4_ru}{uzbek && item.document_4_uz}</p>
                                </div>
                                <div className="data">
                                    <h5>{t("doc9")}</h5>
                                    <p>{english && item.document_5_en}{russian && item.document_5_ru}{uzbek && item.document_5_uz}</p>
                                </div>
                            </div>
                        ))}
                        <div className="col-12 link">
                            <div className='explore' onClick={() => changeContactModal()}>{t("explore3")} <ArrowForward className='line' /></div>
                        </div>
                    </div>
                ))}
                <div className="information">
                    <h1 className="title">{t("fuq10")}</h1>
                    <p className="texts col-8">{t("fuq11")}</p>
                </div>
            </div>
            {showContactModal &&
                <ContactModal close={changeContactModal} nameValue={nameValue} invalidName={invalidName} changeName={changeName} numberValue={numberValue} invalidNumber={invalidNumber} changeNumber={changeNumber} changeContactModal={sendMessage} />
            }
        </>
    );
};

export default OneCitizen;