import React, { useState } from "react";
import "./Scss/ForeignCheck.scss";
import GrouIcon from "../../images/grou.png";
import SettingIcon from "../../images/Group.png";
import ShieldIcon from "../../images/Group 2.png";
import TravelBackg from "../../images/travelBacg.png";
import ContactModal from "../ContactModal/ContactModal";
import { useTranslation } from "react-i18next";

function ForeignCheck({ showContactModal, changeContactModal, dataCountries, uzbek, english, russian, filterData, inputValue, selectData, changeModal }) {

  const [nameValue, setNameValue] = useState("");
  const [numberValue, setNumberValue] = useState("");
  const [showSearch, setShowSearch] = useState(false);
  const [invalidName, setInvalidName] = useState(false);
  const [invalidNumber, setInvalidNumber] = useState(false);

  function selectValue(item) {
    selectData(item)
    setShowSearch(false)
  };

  function changeValue(e) {
    filterData(e)
  };

  function changeName(e) {
    setNameValue(e.target.value)
    setInvalidName(false)
  };

  function changeNumber(e) {
    setNumberValue(e.target.value)
    setInvalidNumber(false)
  };

  let bot = {
    TOKEN: "5796375072:AAEFP5ieQGW4bGvtg9UBdu_7n4304uS97Sw",
    chatID: "-1001830157246",
    message: `%0ADavlat nomi: ${inputValue}; %0AIsmi: ${nameValue}; %0ATelefon raqami: ${numberValue}.`
  };

  function sendMessage() {
    if (nameValue === "") {
      setInvalidName(true)
    } else if (numberValue === "") {
      setInvalidNumber(true)
    } else {
      fetch(`https://api.telegram.org/bot${bot.TOKEN}/sendMessage?chat_id=${bot.chatID}&text=${bot.message} `, {
        method: "GET"
      })
        .then(success => {
          console.log('send message')
        }, error => {
          console.log(error)
        })

      changeContactModal()
      changeModal()
      selectValue("")
      setNameValue("")
      setNumberValue("")
    }
  };

  const { t } = useTranslation();

  return (
    <>
      <div className="ForeignCheck">
        <div className="wrapper">
          <div className="treeth-page">
            <img
              src={TravelBackg}
              alt="background-img"
              className="background-img"
            />
            <div className="contract"></div>
          </div>
          <div className="col-12 foreign-card">
            <div className="body">
              <h3 className="foreign-h3">{t("chet1")}</h3>
              <p className="foreign-p">{t("chet2")}</p>
              <div className="forms col-12">
                <input type="text" className="foreign-input" placeholder={`${t("chet3")}`} value={inputValue} onChange={(e) => changeValue(e)} onClick={() => setShowSearch(true)} />
                <i className="fa fa-angle-down" onClick={() => setShowSearch(!showSearch)}></i>
                <button type="btn" className="submit-btn" onClick={() => changeContactModal()}>{t("chet4")}</button>
                <div className={`contrast ${!showSearch && "d-none"}`} onClick={() => setShowSearch(false)}></div>
                <ul className={`search-bar ${!showSearch && "d-none"}`}>
                  {dataCountries.sort((a,b) => uzbek ? a.name_uz > b.name_uz ? 1 : -1 : a.name.common > b.name.common ? 1 : -1).map((data) => (
                    <li key={data.id} onClick={() => selectValue(english ? data.name.common : russian ? data.translations.rus.common : data.name_uz)}>{english && data.name.common} {russian && data.translations.rus.common} {uzbek && data.name_uz}</li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
          <div className="col-12 icons">
            <div className="col-4 icon">
              <div className="body">
                <div className="img">
                  <img src={SettingIcon} alt="setting-icon" className="img-icon" />
                </div>
                <div className="icon-texts">
                  <h1 className="name">{t("icon1")}</h1>
                  <p className="desc">{t("icon2")}</p>
                </div>
              </div>
            </div>
            <div className="col-4 icons">
              <div className="body">
                <div className="img">
                  <img src={ShieldIcon} alt="shield-icon" className="img-icon" />
                </div>
                <div className="icon-texts">
                  <h1 className="name">{t("icon3")}</h1>
                  <p className="desc">{t("icon4")}</p>
                </div>
              </div>
            </div>
            <div className="col-4 icon">
              <div className="body">
                <div className="img">
                  <img src={GrouIcon} alt="grou-icon" className="img-icon" />
                </div>
                <div className="icon-texts">
                  <h1 className="name">{t("icon5")}</h1>
                  <p className="desc">{t("icon6")}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {showContactModal &&
        <ContactModal close={changeContactModal} nameValue={nameValue} invalidName={invalidName} changeName={changeName} numberValue={numberValue} invalidNumber={invalidNumber} changeNumber={changeNumber} changeContactModal={sendMessage} />
      }
    </>
  );
}

export default ForeignCheck;
