import React from "react";
import "./Scss/Footer.scss";
import { Link } from "react-router-dom";
import Logo from "../../images/logo.png";
import { useTranslation } from "react-i18next";

function Footer({ changeScrollAbt, changeScrollBlog, changeScrollTrav }) {

  const { t } = useTranslation();

  return (
    <div className="Footer">
      <div className="wrapper">
        <div className="footer-nav">
          <a href="/" className="logo navbar-brand">
            <img src={Logo} alt="logo" />
          </a>
          <div className="icons">
            <a href="https://www.instagram.com/swiftvisa.uz" className="insta icon">
              <div className="icon-a">
                <i className="fa fa-instagram"></i>
              </div>
            </a>
            <a href="https://t.me/swiftvisauz" className="telegram icon">
              <div className="icon-a">
                <i className="fa fa-telegram"></i>
              </div>
            </a>
            <a href="tel:+99897-445-44-48" className="whatsapp icon">
              <div className="icon-a">
                <i className="fa fa-whatsapp"></i>
              </div>
            </a>
            <a href="tel:+99897-445-44-48" className="twitter icon">
              <div className="icon-a">
                <i className="fa fa-phone"></i>
              </div>
            </a>
            <a href="https://www.facebook.com/swiftvisa.uz" className="facebook icon">
              <div className="icon-a">
                <i className="fa fa-facebook"></i>
              </div>
            </a>
          </div>
        </div>
        <hr className="hr" />
        <h1 className="company">{t("footer1")}</h1>
        <div className="nav">
          <div className="items">
            <Link to="/" className="item" onClick={changeScrollAbt}>
              {t("footer2")}
            </Link>
            <Link to="/citizenship" className="item">
              {t("footer3")}
            </Link>
            <Link to="/citizenship" className="item">
              {t("footer4")}
            </Link>
            <Link to="/" className="item" onClick={changeScrollBlog}>
              {t("footer5")}
            </Link>
            <Link to="/" className="item" onClick={changeScrollTrav}>
              {t("footer8")}
            </Link>
          </div>
          <a href="#contacts" className="contact-us">
            <div className="contact">
              {t("footer6")}
            </div>
          </a>
        </div>
        <div className="copyright">
          {/* <i class="fa fa-copyright" aria-hidden="true"></i> */}
          <p>{t("footer7")}</p>
        </div>
      </div>
    </div>
  );
}

export default Footer;
